import React from 'react';
import { Circle } from 'react-konva';

const canvasWidth = window.innerWidth;
const canvasHeight = window.innerHeight;

export class Contracts extends React.Component {
    constructor(props) {
        super(props);
        console.log(props)
        this.state = { 
            isMouseInside: false, 
            coordinates: null
        };
        this.handleMouseEnter = this.handleMouseEnter.bind(this);
        this.handleMouseLeave = this.handleMouseLeave.bind(this);
    }
    componentDidMount() {
          this.setState({
            ...this.state,
            coordinates: this.props.coordinates
          });
      }
    
    handleClick(coordinate) {
        console.log(coordinate)
        console.log('clicked this contract')
        this.props.setContract(coordinate)
    }
    handleMouseEnter() {
        this.setState({ isMouseInside: true});
    }
    handleMouseLeave() {
        this.setState({ isMouseInside: false});
    }
    render() {
        const { coordinates, isMouseInside } = this.state
        return (
            <>
            {
                (coordinates &&
                coordinates.length) &&
                coordinates.map((coordinate,index)=>{
                    let locationX = (canvasWidth * coordinate.x) / 100;
                    let locationY = (canvasHeight * coordinate.y) / 100;
                   return (
                       {/* <Circle
                            id={`contract-${index}`}
                            x={locationX} y={locationY} radius={10}
                            fill="yellow" stroke="black"
                            strokeWidth={isMouseInside ? 3 : 1}
                            onClick={()=>this.handleClick(coordinate)}
                            onMouseEnter={this.handleMouseEnter}
                            onMouseLeave={this.handleMouseLeave}
                        /> */}
                    )
                })
            }
                
            </>
        );
    }
}